.chat{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
    height: 70px;
    border-bottom: rgb(139, 139, 139);
}

.chat_details{
    flex: 1;
}

a {
    text-decoration: none;
    color: inherit;
}

.chat_details > p {
    color: gray;
}

.chat_timestamp {
    color: lightgray;
}
.chat_image{
    margin-right: 20px;
}