.card{
    position: relative;
    width: 600px;
    padding: 20px;
    max-width: 85vm;
    height: 50vh;
    border-radius: 20px;
    background-size: cover;
    background-position: center;
    box-shadow:rgba(red, green, blue, alpha);
}
.TinderCard_container{
    display: flex;
    justify-content: center;
    margin-top: 5vh;
}

.swipe{
    position: absolute;
}

.card > h3{
    position: absolute;
    bottom: 10px;
    color: white;
}