.chatScreen_text{
    margin-left: 10px;
    background-color: gray;
    padding: 15px;
    border-radius: 20px;
}
.chatScreen_message{
    display: flex;
    align-items: center;
    padding: 20px;
}
.chatScreen_MainText{
    text-align: center;
    padding: 20px;
    color: rgb(93, 89, 89);
}
.chatSCreen_textUser{
    margin-left: auto;
    background-color: rgb(69, 69, 240);
    padding: 15px;
    border-radius: 20px;
    color: white;
}
.chatSCreen_text{
    background-color: rgb(155, 155, 155);
    padding: 15px;
    border-radius: 20px;
    color: white;
}
.ChatScreen_input{
    display: flex;
    padding: 5px;
    position: fixed;
    bottom: 0px;
    width: 100%;
    border-top: 1px solid lightgray;
}
.ChatScreen_inputField{
    flex: 1px;
    padding: 10px;
    border: none;
}
.chatScreen_btn{
    border: none;
    margin-right: 20px;
    background-color: white;
    font-weight: bold;
    color: red;
}